<template>
  <div style="max-width: 100%;box-sizing: border-box" class="getFeedRecordDetail">
    <myHeader
      :isBack="true"
      backPath="/getFeedRecord"
      title="领料详情">
    </myHeader>
    <div class="seat-head"></div>
    <div class="list-block">
      <mt-cell title="批次号：">
        {{batchNum}}
      </mt-cell>
      <mt-cell title="计划拉料日期：">
        {{drawingMaterialDate}}
      </mt-cell>
      <mt-cell title="拉料司机姓名：" :value="driverName"></mt-cell>
      <mt-cell title="拉料司机身份证号：" :value="driverID"></mt-cell>
      <mt-cell title="拉料司机车牌号码：" :value="driverCarID"></mt-cell>
    </div>
    
    <div class="select-type">
      <div class="title">
        请选择申请类别
      </div>
      <div class="radio-select-box">
        <div class="radio-item">
          <span>领料</span>
          <img v-if="selectedType==0" src="../../../assets/select-icon.png">
          <img v-else src="../../../assets/noSelect-icon.png">
        </div>
        <div class="radio-item">
          <span>退料</span>
          <img v-if="selectedType==1" src="../../../assets/select-icon.png">
          <img v-else src="../../../assets/noSelect-icon.png">
        </div>
      </div>
    </div>
    
    <div class="apply-detail">
      <div class="top">
        <span v-if="selectedType==0">申请领料明细</span>
        <span v-if="selectedType==1">申请退料明细</span>
      </div>
      <div class="detail-item" v-for="(item,index) in getFeedList" :key="index">
        <div class="select-detail">
          <div class="left">
            <span class="left-name">饲料品种：</span>
            <span>{{item.feedName}}</span>
          </div>
          <div class="right">
            <span>包数：</span>
            <span>{{item.bclybs}}</span>
          </div>
        </div>
        <div class="select-result">
          <div>
            <span>已领：{{item.sumReceve}}</span>
            <span>定额：{{item.quotarecevenum}}</span>
          </div>
        </div>
      </div>
    </div>
    
    <div class="result-apply-detail" v-if="dsStatus && dsStatus==30">
      <div class="top">
        <span>实际领用明细</span>
      </div>
      <div class="details-container" v-for="(item,index) in actEntrys" :key="index">
        <div class="detail-item">
          <div class="left">
            <span>饲料品种：</span>
            <span>{{item.feedName}}</span>
          </div>
          <div class="right">
            <span>数量：</span>
            <span>{{item.bclybs}}</span>
          </div>
        </div>
        <div class="detail-item">
          <div class="left">
            <span>饲料单价：</span>
            <span>{{item.price}}</span>
          </div>
          <div class="right">
            <span>金额：</span>
            <span>{{item.amount}}</span>
          </div>
        </div>
<!--        <div class="detail-item">-->
<!--          <div class="left">-->
<!--            <span>已领数额：</span>-->
<!--            <span>{{item.sumReceve}}</span>-->
<!--          </div>-->
<!--          <div class="right">-->
<!--            <span>定额：</span>-->
<!--            <span>{{item.quotarecevenum}}</span>-->
<!--          </div>-->
<!--        </div>-->
      </div>
    </div>
    
    <Signature v-show="isShowSignature" ref="signature"></Signature>
    
    <SubmitBtn
      v-if="recordState != 2  && dsStatus==30"
      @click.native="submitData()">
    </SubmitBtn>
  
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import SubmitBtn from "@/components/SubmitBtn";
  import Signature from "@/components/Signature";
  import {viewInfoByIdFeed, saveSignerFeedApply} from '@/api/materialApply'
  import {getParam} from '@/utils/wxUtil'
  import {Indicator} from 'mint-ui'
  
  export default {
    name: 'getFeedRecordDetail',
    data() {
      return {
        batchNum: "",
        drawingMaterialDate: '',
        driverName: '',
        driverID: '',
        driverCarID: '',
        selectedType: 0,
        getFeedList: [{
          feedName: '',
          bclybs: '',
          quotarecevenum: '',
          sumReceve: ''
        }],
        actEntrys: [{
          feedName: '',
          bclybs: '',
          price: '',
          amount: '',
          quotarecevenum: '',
          sumReceve: ''
        }],
        rcordId: '',
        recordState: '',
        dsStatus: '',
        signerImgs: '',
        isShowSignature: true
      }
    },
    components: {
      Signature,
      SubmitBtn,
      myHeader
    },
    created() {
      this._viewInfoByIdFeed();
      // this.rcordId = this.$route.params.rcordId;
      this.rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    watch: {
      recordState(newName, oldName) {
        // 已完成签名，只需要展示签名图片，不能编辑
        if (this.recordState == 2 &&  this.dsStatus == 30) {
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.fromDataURL(this.signerImgs);
            this.$refs.signature.disabled = true;
          });
        }
    
        if (this.dsStatus == 30 && this.recordState != 2) { // 已经审核，可以签字，可以编辑
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = false;
          });
        }
    
        if (this.recordState == 3 || this.dsStatus != 30) {  // 没有审核或者已经取消
          this.isShowSignature = false;
        }
    
      }
    },
    methods: {
      _viewInfoByIdFeed() {
        // const rcordId = this.$route.params.rcordId;
        const rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
        viewInfoByIdFeed(rcordId).then(res => {
          if (res.data) {
            Indicator.close();
            let data = res.data;
            data.state ? this.recordState = data.state : null;
            data.dsStatus ? this.dsStatus = data.dsStatus : null;
            data.signerUrl.length ? this.signerImgs = data.signerUrl[0].img : null;
            
            this.batchNum = data.batchName ? data.batchName : '';
            this.drawingMaterialDate = data.plandate ? data.plandate : '';
            this.driverName = data.driverName ? data.driverName : '';
            this.driverID = data.driverIdcard ? data.driverIdcard : '';
            this.driverCarID = data.driverVehicleno ? data.driverVehicleno : '';
            this.selectedType = data.type ? data.type : '';
            
            this.getFeedList = data.pickDetail.map(item => {
              return {
                feedName: item.feedName,
                bclybs: item.bclybs,
                quotarecevenum: item.quotarecevenum,
                sumReceve: item.sumReceve
              }
            });
            
            this.actEntrys = data.actEntrys.map(item => {
              return {
                feedName: item.feedName,
                bclybs: item.bclybs,
                price: item.price,
                amount: item.amount,
                quotarecevenum: item.quotarecevenum,
                sumReceve: item.sumReceve
              }
            });
          } else {
            Indicator.close();
          }
          
          
        }, err => {
          Indicator.close();
        });
      },
      
      submitData() {
        let signerImg = this.$refs.signature.save();
        
        const data = {
          rcordId: this.rcordId,
          fileEntry: {
            imgContent: signerImg,
            imgType: 'png'
          }
        };
        
        saveSignerFeedApply(data).then(res => {
          this.$toast({
            message: '签字确认成功！',
            duration: 1000
          });
          this.$router.push('/getFeedRecord');
        });
        
      }
    }
  }
</script>

<style lang="scss">
  .getFeedRecordDetail {
    min-height: 100vh;
    background-color: #F8F8F8;
    font-size: .16rem;
    
    .head {
      background-color: #FFF;
    }
    
    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;
    }
    
    .select-type {
      background-color: #fff;
      margin-top: .1rem;
      font-size: .16rem;
      
      .title {
        height: .45rem;
        padding: .1rem .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .radio-select-box {
        padding-left: .15rem;
        border-bottom: 1px solid #dddddd;
        
        .radio-item {
          display: flex;
          justify-content: space-between;
          height: .52rem;
          line-height: .52rem;
          padding-left: .05rem;
          padding-right: .15rem;
          
          &:first-child {
            border-bottom: 1px solid #dddddd;
          }
          
          img {
            align-self: center;
          }
        }
      }
    }
    
    .apply-detail {
      font-size: .16rem;
      background-color: #fff;
      margin-top: .1rem;
      
      .top {
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .detail-item {
        padding-left: .2rem;
        border-bottom: 1px solid #dddddd;
        
        .select-detail {
          display: flex;
          flex-wrap: nowrap;
          justify-content: space-between;
          align-items: center;
          height: .54rem;
          font-size: .14rem;
          border-bottom: 1px solid #dddddd;
          padding-right: .23rem;
          
          .left {
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            .left-name {
              color: #585858;
              padding-right: .05rem;
            }
            
            .img {
              width: .1rem;
              height: .06rem;
              margin-left: .08rem;
              background: url('~@/assets/icon-next.png') no-repeat;
              background-size: 100%;
            }
          }
          
          .right {
            text-align: right;
            flex-grow: 0.5;
          }
        }
        
        .select-result {
          font-size: .12rem;
          color: #F89B31;
          height: .37rem;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          padding-right: .43rem;
          
          span:nth-child(1) {
            margin-right: .2rem;
          }
        }
      }
    }
    
    .result-apply-detail {
      font-size: .16rem;
      margin-top: .1rem;
      
      .top {
        background-color: #fff;
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .details-container {
        background-color: #fff;
        margin-bottom: .05rem;
        padding-left: .2rem;
        border-bottom: 1px solid #EBEBEB;
        
        .detail-item {
          padding-right: .15rem;
          font-size: .14rem;
          display: flex;
          height: .54rem;
          justify-content: space-between;
          line-height: .54rem;
          border-bottom: 1px solid #EBEBEB;
          
          .left {
            width: 50%;
            padding-left: 2.5%;
            
            .tip-text {
              color: #888;
            }
          }
          
          .right {
            width: 40%;
            padding-left: 2.5%;
          }
          
          &:last-child {
            border-bottom: 0;
          }
        }
      }
      
    }
  }
</style>



